.barrier-configurator .bc__button {
  border: none;
  cursor: pointer;
  transition: all 0.3s;
  color: var(--color-white);
  font-family: inherit;
  font-size: 18px;
  font-weight: 600;
  line-height: 120%;
  border-radius: 14px;
  min-height: 48px;
  padding-left: 36px;
  padding-right: 36px;
  width: 100%;
}

.barrier-configurator .bc__button:disabled, .barrier-configurator .bc__button:disabled:active, .barrier-configurator .bc__button.disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.barrier-configurator .bc__button--full {
  max-width: 100%;
}

.barrier-configurator .bc__button--outline {
  background-color: transparent;
  border: 2px solid var(--color-white);
}

.barrier-configurator .bc__button--outline:hover:not(:disabled) {
  background-color: var(--color-black);
  border-color: var(--color-black);
}

.barrier-configurator .bc__button--black {
  background-color: var(--text-black);
  color: var(--color-white);
  padding: 20px 32px;
  border-radius: 20px;
}

.barrier-configurator .bc__button--black:hover:not(:disabled) {
  background-color: var(--color-black);
}

.barrier-configurator .bc__button__burger, .barrier-configurator .bc__button__phone {
  border: none;
  cursor: pointer;
  transition: all 0.3s;
  padding: 0;
  background-color: transparent;
  outline: none;
}

.barrier-configurator .bc__button--wrapper {
  margin-top: 40px;
}

@media screen and (min-width: 451px) {
  .barrier-configurator .bc__button {
    border-radius: 24px;
    font-size: 20px;
  }
  .barrier-configurator .bc__button--wrapper {
    margin-top: 52px;
  }
}

@media screen and (min-width: 901px) {
  .barrier-configurator .bc__button:not(.bc__button--full) {
    max-width: 360px;
  }
}

@media screen and (min-width: 1601px) {
  .barrier-configurator .bc__button--wrapper {
    margin-left: 52px;
  }
}

@media screen and (min-width: 1901px) {
  .barrier-configurator .bc__button--wrapper {
    margin-top: 64px;
  }
}
